.SortInfo {
    text-align: left;
    width: 80%;
    max-width: 1200px;
    margin: 0 auto;
  }
  .SortInfo h1 {
    margin: 1.5rem 0;
  }
  .SortInfo__Body {
    display: flex;
    flex-flow: row wrap;
  }
  
  .SortInfo__Article {
    width: 50%;
    line-height: 1.3;
  }
  
  .SortInfo__Article li {
    line-height: 1.5;
  }

  .SortInfo__Aside {
    margin-top: 3rem;
  }
  
  .SortInfo__Aside h3 {
    margin: 1rem 0;
  }
  
  .SortInfo__Aside code {
    padding-left: 1rem;
    font-weight: 700;
  }
  @media (max-width: 800px){
    .SortInfo__Body{
      flex-direction: column;
    } 
    .SortInfo__Article{
      width: 100%;
    }
  }
  /* Large devices (desktops, 992px and up) */
  @media (min-width: 992px) {
    .SortInfo__Aside {
      padding-left: 2ch;
    }
  }
  
  /* Extra large devices (large desktops, 1200px and up) */
  @media (min-width: 1200px) {
    .SortInfo__Article {
      width: 55%;
      padding-right: 5ch;
    }
  
    .SortInfo__Aside {
      padding-left: 5ch;
    }
  }
  